.table__tr {
  border-bottom: 1px solid #f5f5f5;
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.24, 0.22, 0.015, 1.56);
}
.table__tr:nth-of-type(even) {
  background: #fafafa;
}
.table__tr:not(.table__tr--thead):hover {
  box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
}

.table__th {
  padding: 0 0.25rem;
  height: 2.5rem;
  vertical-align: middle;
  text-align: left;
  font-size: 0.9rem;
