  cursor: pointer;
  transition: color 0.2s;
}
.table__th.table__th--sortable:hover {
  color: rgba(0, 0, 0, 0.54);
}
.table__th.table__th--sortable:hover::after {
  opacity: 1;
}
.table__th.table__th--sortable::after {
  content: "arrow_drop_up";
  font-family: "Material Icons";
  display: inline-block;
  font-size: 1rem;
  position: relative;
  top: 0.2rem;
  opacity: 0;
  transition: opacity 0.2s;
}
.table__th.table__th--sortable-asc {
  color: #3f51b5;
}
.table__th.table__th--sortable-asc::after {
  opacity: 1;
  transition: transform 0.2s;
}
.table__th.table__th--sortable-asc:hover {
  color: #3f51b5;
}
.table__th.table__th--sortable-asc:hover::after {
  transform: rotate(180deg);
}
.table__th.table__th--sortable-desc {
  color: #3f51b5;
}
.table__th.table__th--sortable-desc::after {
  opacity: 1;
  transform: rotate(180deg);
  transition: transform 0.2s;
}
.table__th.table__th--sortable-desc:hover {
  color: #3f51b5;
}
.table__th.table__th--sortable-desc:hover::after {
  transform: rotate(0deg);
}

.table__td {
  padding: 0 0.25rem;
  height: 3.5rem;
  vertical-align: middle;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.54);
  position: relative;
}

.table__action-icon {
  cursor: pointer;
  font-size: 1.25rem;
  padding: 0.5rem;
  color: rgba(0, 0, 0, 0.54);
  transition: color 0.2s;
  margin: -0.5rem -0.25rem;
}
.table__action-icon:hover, .table__action-icon:focus {
  color: #3f51b5;
}

.table__onboarding {
  display: block;
  width: 6rem;
  text-align: center;
  padding: 0.5rem 0;
  border-radius: 1rem;
  color: #ffffff;
  font-weight: 700;
}
.table__onboarding.table__onboarding--completed {
  background: linear-gradient(45deg, #3f51b5 0, #7a87d1 50%, #3f51b5 100%);
}
.table__onboarding.table__onboarding--pending {
  background: linear-gradient(45deg, #ffc107 0, #ffd454 75%, #ffc107 100%);
}

.table__dots {
  cursor: pointer;
  display: inline-block;
  padding: 0.5rem;
}
.table__dots::before {
  content: "";
  display: block;
  width: 0.3rem;
  height: 0.3rem;
  background: rgba(0, 0, 0, 0.54);
  border-radius: 50%;
  box-shadow: -0.5rem 0 0 0 rgba(0, 0, 0, 0.54), 0.5rem 0 0 0 rgba(0, 0, 0, 0.54);
  transition-property: background, box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.24, 0.22, 0.015, 1.56);
}
.table__dots:hover::before {
  background: #3f51b5;
  box-shadow: 0.5rem 0 0 0 #3f51b5, -0.5rem 0 0 0 #3f51b5;
}

.table__toggler {
  display: none;
}
.table__toggler:checked ~ .table__dots::before {
  background: #3f51b5;
  box-shadow: 0 -0.5rem 0 0 #3f51b5, -0.5rem 0.25rem 0 0 #3f51b5, 0.5rem 0.25rem 0 0 #3f51b5, -0.25rem 0.5rem 0 0 #3f51b5, 0.25rem 0.5rem 0 0 #3f51b5, 0 0.75rem 0 0 #3f51b5;
}
.table__toggler:checked ~ .table__more-actions .table__action {
  height: 3rem;
  padding: 0.25rem 0.5rem;
}
.table__toggler:checked ~ .table__more-actions .table__action:hover {
  background: #f5f5f5;
  box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
}
.table__toggler:checked ~ .table__more-actions .table__action .material-icons {
  display: inline;
}
.table__more-actions {
  top: 100%;
  right: 0;
  position: absolute;
  background: #ffffff;
  box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
  overflow: hidden;
  top: 100%;
  z-index: 2;
}
.table__action {
  z-index: 3;
  height: 0;
  margin: 0;
  width: 10rem;
  display: flex;
