.calendar {
  background: #ffffff;
  box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
  padding: 1rem;
}
.calendar__month-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 0.5rem;
}
.calendar__switcher {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  cursor: pointer;
  padding: 1rem;
  margin: -1rem;
}
.calendar__switcher:hover {
  color: #ffc107;
}
.calendar__switcher::after {
  font-family: "Material Icons";
}
.calendar__switcher.calendar__switcher--left:after {
  content: "keyboard_arrow_left";
}
.calendar__switcher.calendar__switcher--right:after {
  content: "keyboard_arrow_right";
}

.calendar__labels-wrapper {
  display: flex;
  flex-direction: row;
  width: 21rem;
}
.calendar__label {
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.38);
}

.calendar__days-wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 21rem;
}
.calendar__day {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  cursor: pointer;
  position: relative;
  transition: box-shadow 0.2s;
}
.calendar__day::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%;
  height: 70%;
  border-radius: 50%;
  z-index: -1;
}
.calendar__day:hover::before {
  box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.15);
}
.calendar__day.calendar__day--inactive {
  color: rgba(0, 0, 0, 0.38);
  opacity: 0.5;
  transition: opacity 0.2s;
}
.calendar__day.calendar__day--inactive:hover {
  opacity: 1;
}
.calendar__day.calendar__day--checked {
  color: #ffffff;
}
.calendar__day.calendar__day--checked::before {
  background: linear-gradient(45deg, #ffc107 0, #ffd454 75%, #ffc107 100%);
  box-shadow: 0 0.75rem 1.25rem rgba(255, 193, 7, 0.2);
}
.calendar__day.calendar__day--today {
  color: #ffc107;
  font-weight: 700;
}

.pie-chart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 1.25rem;
}
.pie-chart__wrapper {
  position: relative;
}
.pie-chart__background {
  width: 10rem;
  height: 10rem;
  background: #fafafa;
  box-shadow: 0 0.75rem 1.25rem rgba(63, 81, 181, 0.2);
  border-radius: 50%;
}
.pie-chart__inner-circle {
  fill: #ffffff;
  opacity: 0.8;
  stroke-width: 100%;
  stroke: #3f51b5;
  stroke-dasharray: 0, 31.4159265359rem;
  transform: rotate(-90deg);
  transform-origin: center;
  transition-property: stroke-dasharray;
  transition-duration: 0.2s;
  transition-timing-function: cubic-bezier(0.24, 0.22, 0.015, 1.56);
