.slider_mask {
  overflow: hidden;
  position: relative;
  padding: 1rem 0;
}
.slider_mask::after, .slider_mask::before {
  content: "";
  position: absolute;
  top: 0;
  width: 2rem;
  height: 100%;
  z-index: 1;
}
.slider_mask::before {
  left: 0;
  background-image: linear-gradient(to right, #fafafa, transparent 50%);
}
.slider_mask::after {
  right: 0;
  background-image: linear-gradient(to right, transparent, #fafafa 50%);
}
.slider__wrapper {
  display: flex;
  flex-direction: row;
  width: calc(calc(100% / 4) * 6);
  animation: slideLeftRight 15s linear infinite alternate-reverse;
}
.slider__wrapper:hover {
  animation-play-state: paused;
}

.slider__card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  width: calc(100% / 6 - 2rem);
  background: #ffffff;
  box-shadow: 0 0.25rem 1rem rgba(0, 0, 0, 0.15);
  margin: 0 1rem;
}
.slider__icons-wrapper {
  display: flex;
}
.slider__icon_wrapper {
  width: calc(100% / 3);
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 1rem 0;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.54);
  transition: color 0.2s;
}
.slider__icon_wrapper:hover, .slider__icon_wrapper:focus {
  color: #3f51b5;
}

.slider__star-checkbox {
  display: none;
}
.slider__star-checkbox:checked + .slider__star-icon::before {
  display: none;
}
.slider__star-checkbox:checked + .slider__star-icon::after {
  display: inline;
}

.slider__star-icon {
  cursor: pointer;
}
.slider__star-icon::before, .slider__star-icon::after {
  height: 100%;
  width: 100%;
  font-family: "Material Icons";
  font-size: 1.5rem;
  transition: color 0.2s;
}
.slider__star-icon::before {
  content: "star_border";
  display: inline;
}
.slider__star-icon::after {
  content: "star";
  display: none;
  color: #ffc107;
}
.slider__star-icon:hover, .slider__star-icon:focus {
  color: #ffc107;
}

.slider__info-wrapper {
  padding: 1rem;
  margin: 0.5rem 0;
}
.slider__wrapper-avatar {
  border-radius: 50%;
  max-width: 100%;
  box-shadow: 0 0.75rem 1.25rem rgba(0, 0, 0, 0.2);
}

.slider__name {
  margin-top: 1.5rem;
  color: rgba(0, 0, 0, 0.54);
}

.slider__position {
  font-size: 1.25rem;
  margin-top: 0.5rem;
  display: block;
}

@keyframes slideLeftRight {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc( -100% / 6 * (6 - 4) ));
  }
}
@media (max-width: 75rem) {
  .slider__wrapper {
    width: calc(calc(100% / 3) * 6);
  }
}
@media (max-width: 62rem) {
  .slider__wrapper {
    width: calc(calc(100% / 2) * 6);
  }
}
@media (max-width: 30rem) {
  .slider__wrapper {
    width: calc(calc(100% / 1) * 6);
  }

  .slider__info-wrapper {
    padding: 0 1rem;
  }
}